@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
/*======= MEDIA QUERIES ======*/
/*=====COLORS =====*/
html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  line-height: 1.5;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.section {
  padding: 7.5rem 1.6rem;
}
@media screen and (min-width: 769px) {
  .section {
    padding: 11rem 1.6rem;
  }
}

