h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Poppins", sans-serif;
}

h2 {
  font-size: 4rem;
  font-family: "Poppins", sans-serif;
}

h3 {
  font-size: 3rem;
}

a {
  text-decoration: none;
  color: #000000;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: transparent;
  z-index: 100;
}

.nav {
  margin-top: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 65px;
}
@media screen and (min-width: 769px) {
  .nav {
    height: calc(65px + 5rem);
  }
}
.nav__logo {
  width: 18rem;
}
@media screen and (min-width: 769px) {
  .nav__logo {
    width: 25rem;
  }
}
.nav__toggle {
  font-size: 1.8rem;
  cursor: pointer;
}
@media screen and (min-width: 769px) {
  .nav__toggle {
    display: none;
  }
}

@media screen and (max-width: 769px) {
  .menu {
    position: absolute;
    top: 0;
    right: -100%;
    height: 100%;
    width: 100%;
    transition: 0.3s;
    background-color: #f0f0f0;
  }
}
.menu__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 24.2rem;
}
@media screen and (min-width: 769px) {
  .menu__list {
    flex-direction: row;
    padding-top: 0;
    column-gap: 2rem;
  }
}
.menu__item {
  margin-bottom: 2rem;
}
@media screen and (min-width: 769px) {
  .menu__item {
    margin-bottom: 0;
  }
}
.menu__links {
  color: #000000;
  text-decoration: none;
  font-weight: 500;
  font-size: 2.2rem;
  transition: 0.3s ease-in-out;
}
.menu__links:hover {
  color: #4e6eaf;
}
@media screen and (min-width: 769px) {
  .menu__links {
    font-size: 1.8rem;
    font-weight: normal;
    color: #ffffff;
  }
}
.menu__close {
  font-size: 1.8rem;
  cursor: pointer;
  position: absolute;
  top: 2rem;
  right: 2rem;
}
@media screen and (min-width: 769px) {
  .menu__close {
    display: none;
  }
}

.about {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../img/header_bg.jpg);
  background-size: cover;
  background-position: center;
  padding: 3rem 0;
  position: relative;
}
@media screen and (min-width: 769px) {
  .about {
    padding: 9rem 0 20rem 0;
  }
}
.about__container {
  padding: 15rem 0 5rem 0;
}
@media screen and (min-width: 769px) {
  .about__container {
    margin-top: 10rem;
    padding: 0;
    max-width: 50rem;
  }
}
.about__title {
  color: #ffffff;
  text-align: center;
  font-size: 3.8rem;
  margin: 2rem 0;
}
@media screen and (min-width: 769px) {
  .about__title {
    text-align: left;
  }
}
.about__info {
  color: #ffffff;
  text-align: center;
}
@media screen and (min-width: 769px) {
  .about__info {
    text-align: left;
  }
}
.about__image {
  display: none;
}
@media screen and (min-width: 769px) {
  .about__image {
    display: block;
    position: absolute;
    right: 0;
    bottom: -6rem;
    max-width: 35rem;
    border-top-left-radius: 5rem;
    border-bottom-left-radius: 5rem;
  }
}
@media screen and (min-width: 1200px) {
  .about__image {
    max-width: 60rem;
  }
}

.prop {
  margin-top: 2rem;
}
@media screen and (min-width: 769px) {
  .prop {
    margin-top: 0;
  }
}
.prop__title {
  font-weight: 400;
  font-size: 3.2rem;
}
.prop__title span {
  font-weight: 700;
}
.prop__title::after {
  content: "";
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
  display: block;
  width: 15rem;
  height: 1rem;
  margin-top: 1rem;
}
.prop__grid {
  margin-top: 4rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media screen and (min-width: 769px) {
  .prop__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media screen and (min-width: 1024px) {
  .prop__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.propiedad {
  background-color: #ffffff;
}
.propiedad__image {
  aspect-ratio: 10/6;
  background-size: auto 150%;
  position: relative;
}
.propiedad:nth-child(1) .propiedad__image {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_1.jpg");
}
.propiedad:nth-child(2) .propiedad__image {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_2.jpg");
}
.propiedad:nth-child(3) .propiedad__image {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_3.jpg");
}
.propiedad:nth-child(4) .propiedad__image {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_4.jpg");
}
.propiedad:nth-child(5) .propiedad__image {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_5.jpg");
}
.propiedad:nth-child(6) .propiedad__image {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_6.jpg");
}
.propiedad__name {
  position: absolute;
  color: #ffffff;
  font-size: 2.4rem;
  bottom: 1rem;
  left: 1rem;
}
.propiedad__content {
  padding: 2rem;
}
.propiedad__info {
  margin-bottom: 0;
}
.propiedad__precio {
  font-size: 2.8rem;
}
.propiedad__button {
  background-color: #3b4862;
  padding: 1rem 4rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 1.8rem;
  font-weight: 400;
  font-weight: 700;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  border-radius: 1rem;
}
@media screen and (min-width: 769px) {
  .propiedad__button {
    width: auto;
  }
}
.propiedad__button:hover {
  background-color: #283142;
}

.desta {
  background-image: linear-gradient(to bottom, transparent 50%, #3b4862 50%, #3b4862 100%), url(../img/destacada.jpg);
  background-repeat: no-repeat;
  background-position: 50%, 50% -30rem;
  background-size: 100%, cover;
}
@media screen and (min-width: 769px) {
  .desta {
    background-image: linear-gradient(to right, transparent 50%, #3b4862 50%, #3b4862 100%), url(../img/destacada.jpg);
    background-position: 50%, -50rem;
  }
}
.desta__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media screen and (min-width: 769px) {
  .desta__grid {
    grid-template-rows: unset;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.desta__container {
  grid-row: 2/3;
  padding: 5rem 0 0 0;
}
@media screen and (min-width: 769px) {
  .desta__container {
    grid-row: unset;
    grid-column: 2/3;
    padding: 0 5rem;
  }
}
.desta__title {
  color: #ffffff;
  text-transform: uppercase;
}
.desta__info {
  color: #ffffff;
  font-size: 1.8rem;
}
.desta__precio {
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: 600;
}
.desta__button {
  background-color: #ffffff;
  padding: 1rem 4rem;
  color: #3b4862;
  margin-top: 2rem;
  font-size: 1.8rem;
  font-weight: 400;
  font-weight: 700;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  margin-top: 0;
  display: block;
  border-radius: 1rem;
}
@media screen and (min-width: 769px) {
  .desta__button {
    width: auto;
  }
}
.desta__button:hover {
  background-color: #e6e6e6;
}

.client-blog {
  padding: 5rem 0;
}
@media screen and (min-width: 1024px) {
  .client-blog {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}
@media screen and (min-width: 1024px) {
  .client-blog__blog {
    grid-column: 2/4;
  }
}
.client-blog__title {
  font-weight: 400;
  font-size: 3.2rem;
  margin: 3rem 0;
}
.client-blog__title span {
  font-weight: 700;
  display: block;
}
.client-blog__title::after {
  content: "";
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
  display: block;
  width: 15rem;
  height: 1rem;
  margin-top: 1rem;
}

.client {
  text-align: center;
}
.client__imagen {
  margin: 0 auto;
  width: 20rem;
}
.client__container {
  background-color: #ffffff;
  margin-top: -10rem;
  padding: 10rem 2rem 2rem 2rem;
}
.client__subtitle {
  margin: 2rem 0;
  text-transform: capitalize;
  font-size: 2.4rem;
}
.client__desp::before {
  content: "";
  background-image: url(../img/quote.svg);
  background-repeat: no-repeat;
  background-position: 50%;
  width: 4rem;
  height: 4rem;
  display: inline-block;
  margin-right: 1rem;
}

.blog {
  background-color: #ffffff;
  margin-bottom: 2rem;
}
@media screen and (min-width: 769px) {
  .blog {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
    align-items: center;
  }
  .blog:last-of-type {
    margin-bottom: 0;
  }
}
@media screen and (min-width: 769px) {
  .blog__imagen--container {
    grid-column: 1/3;
  }
}
.blog__imagen {
  height: 30rem;
  object-fit: cover;
  width: 100%;
}
.blog__container {
  padding: 1rem;
}
@media screen and (min-width: 769px) {
  .blog__container {
    grid-column: 3/6;
  }
}
.blog__meta {
  font-size: 1.4rem;
}
.blog__button {
  background-color: #3b4862;
  padding: 1rem 4rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 1.8rem;
  font-weight: 400;
  font-weight: 700;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  font-weight: 500;
}
@media screen and (min-width: 769px) {
  .blog__button {
    width: auto;
  }
}
.blog__button:hover {
  background-color: #283142;
}
@media screen and (min-width: 769px) {
  .blog__button {
    display: inline-block;
  }
}

.footer__container {
  background-color: #3b4862;
  padding: 4rem 0;
}
.footer__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media screen and (min-width: 769px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media screen and (min-width: 1024px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer__logo {
  width: 25rem;
  margin: 0 auto;
}
@media screen and (min-width: 769px) {
  .footer__logo {
    margin: 0;
  }
}
.footer__title {
  color: #ffffff;
  margin-bottom: 2rem;
}
.footer__items {
  margin-bottom: 1rem;
}
.footer__items:last-of-type {
  margin-bottom: 0;
}
.footer__links {
  color: #ffffff;
  font-size: 1.8rem;
}
.footer__info {
  color: #ffffff;
  font-size: 1.8rem;
}
@media screen and (min-width: 769px) {
  .footer__info {
    margin-bottom: 0;
  }
}

.copy {
  background-color: #283142;
  padding: 1rem;
}
.copy__alright {
  text-align: center;
  color: #ffffff;
  margin: 0;
  font-size: 1.5rem;
}